//引入网络请求
import $H from '@/common/request.js';
import $C from '@/common/config.js';
import $U from '@/common/util.js';


export default {
	data() {
		return {
			background: {
				backgroundColor: '#fff',
			},
			wordslength:0,
			showPop:false,
			goodsDetailsJson:[
			],
			detailId:[]
			
		}
	},
	
	onLoad(data) {
		if(data.list){
			let list=JSON.parse(data.list)
			console.log(list)
			for (var i = 0; i < list.length; i++) {
				let data={
					type:list[i].type,
					pictureUrl:list[i].pictureUrl,
					text:list[i].text
				}
				this.goodsDetailsJson.push(data)
				
			}
		}
		
		
	},
	
	methods: {
		
		backToSave:function(){
			this.showPop=true
		},
		
		quitDetail(){
			uni.navigateBack({
				delta:1
			})
		},
		
		addContent(index){
			if(index==1){
				let data={
					type:1,
					pictureUrl:"",
					text:""
				}
				this.goodsDetailsJson.push(data)
			}
			else{
				this.toChooseImage()
			}
			
			
			
		},
		
		toChooseImage(){
			
			uni.chooseImage({
			   count: 9, //默认9
			   success: (res) => {
			   	console.log(res.tempFilePaths);
				
				for (var i = 0; i < res.tempFilePaths.length; i++) {
					let data={
						type:2,
						pictureUrl:res.tempFilePaths[i],
						text:""
					}
					this.goodsDetailsJson.push(data)
				}
				
				
			   },
			   fail: (err) => {
			   	console.log(err)
			   }
			});
		},
		
		deleteTextOrImage(index){
			this.goodsDetailsJson.splice(index, 1);
			console.log(this.types)
		},
		
		
		
		saveDetail(){
			
			let data={
				goodsDetailsJson:this.goodsDetailsJson
			}
			
			// 发送到服务端
			$H.get("/goods_details",data).then((res) => {
				//请求成功
				console.log(res)
			    
				for (var i = 0; i < res.data.length; i++) {
					this.detailId.push(res.data[i].id)
				}
			
			    // 获取当前页面栈
			    let pages = getCurrentPages()
			    // pages数组中的最后一个为当前页面，倒数第二个为上一个页面
			    let prevPage = pages[pages.length - 2]
			    
			    // 重点来了
			    // 调用$vm 注册一个自定义方法 将参数传入进去
			    prevPage.onShow(this.detailId)
			    // 然后调用navigateBack返回页面
			    uni.navigateBack({
			      	// 这个属性表示返回1个页面
			      	delta: 1
			    })
			
			}).catch((e) => {
			
				//请求失败
				console.log("失败"+e)
			})
			
		}
	}
}
